.job-search {
	&__row {
		display: grid;
		grid-template-columns: 1fr 1fr 134px;
		grid-template-rows: auto;
		grid-column-gap: 32px;
		width: 100%;
		max-width: 978px;
	}

	&__label {
		margin-top: 0;
		margin-bottom: 34px;
		font-size: 18px;
		line-height: 18px;
		font-weight: 700;
	}
}

